.timeline {
	display: flex;
	margin: 0 auto;
	flex-wrap: wrap;
	flex-direction: column;
	max-width: 700px;
	position: relative;
}

.timeline__content-title {
	font-weight: normal;
	font-size: 66px;
	margin: -10px 0 0 0;
	-webkit-transition: 0.4s;
	transition: 0.4s;
	padding: 0 10px;
	box-sizing: border-box;
	font-family: "Pathway Gothic One", sans-serif;
	color: #fff;
}

/* text */
.timeline__content-desc {
	margin: 0 auto;
	font-size: 15px;
	/* box-sizing: border-box; */
	color: rgba(255, 255, 255, 0.7);
	font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
	font-weight: normal;
	line-height: 25px;
	/* text-align:justify; */
	/* text-align:center; */
	width: 50%;
}

