@media only screen and (max-width: 767px) {
	.timeline-item {
		align-self: baseline !important;
		width: 100%;
		padding: 0 30px 150px 80px;
	}
	.timeline-item:before {
		left: 10px !important;
		padding: 0 !important;
		top: 50px;
		text-align: center !important;
		width: 60px;
		border: none !important;
	}
	.timeline-item:last-child {
		padding-bottom: 40px;
	}
}
.timeline__img {
	max-width: 100%;
	box-shadow: 0 10px 15px rgba(0, 0, 0, 0.4);
}
.timeline-container {
	width: 100%;
	position: relative;
	padding: 80px 0;
	background-attachment: fixed;
	background-size: cover;
}
.timeline-container {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 90%;
	background: rgba(99, 99, 99, 0.8);
	/* content: ""; */
	margin-top: 130px;
}
.timeline-header {
	width: 100%;
	text-align: center;
	margin-bottom: 80px;
	position: relative;
}
/* title */
.timeline-container > .timeline-event__title h2 {
	display: flex;
	flex-direction: column;
	margin-top: 100px;
	color: #fff;
	font-size: 46px;
	font-weight: normal;
	margin: 0;
}
/* event name */
.timeline-header__subtitle {
	color: white;
	/* color: rgba(255, 255, 255, 0.5); */
	font-family: "Pathway Gothic One", sans-serif;
	font-size: 20px;
	letter-spacing: 5px;
	margin: 10px 0 0 0;
	font-weight: normal;
}

/* button selector */
.selectOpt {
	margin: 0 auto;
	width: 160px;
}

.timeline-header > .select-button {
	margin-top: 50px;
}

.selectOpt select {
	display: block;
	font-weight: normal;
	padding: 3rem;
	width: 100%;
	font-size: 16px;
	background-color: #ddd;
	font-family: sans-serif;
	color: #444;
	box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
	border-radius: 0.5em;
	box-sizing: border-box;
}

.selectOpt select:hover {
	border-color: #888;
}

